<template>
  <div id="list">
    <ul>
      <li v-for="person in persons">{{ person.name }}</li>
    </ul>
  </div>
</template>


<script>
  new Vue({
    el: '#list',
    data: {
      persons: []
    },
    created () {
      var vm = this
      axios.get('https://jsonplaceholder.typicode.com/users')
        .then(function (response) {
          vm.persons = response.data
        })
    }
  })
</script>